<template>
    <div>
        <input type="text" v-on:keyup.enter="addComment" v-model="input">
        <div class="buttonBase" v-on:click="addComment"></div>
        
        <div v-for="(comment, index) in comments" :key="index" class="comments" data-v-tl>
            <span>{{comment.body}}</span> <span style="font-size:80%;">by {{comment.footer}}</span>
        </div>
    </div>
</template>

<script>
export default {
    name : "TimeLineComp",

    props: ['limit'],

    computed: {
        comments(){
            return this.$store.getters.comments
        }
    },

    created()
    {
        this.$store.dispatch('GetComments', this.limit)
    },

    methods:{
        async addComment()
        {
            await this.$store.dispatch('AddTLcomment', this.input)
            await this.$store.dispatch('GetComments', this.limit)
            this.input = null
        }
    },

    data: function () {
        return {
            input : null
        }
    }
}
</script>

<style scoped>
    .comments[data-v-tl]
    {
        border-bottom: solid 1px #d9d9d9;
        padding : 20px 5px;
    }

</style>


